// 懒加载路由写法
const index = resolve => require(['./components/index.vue'], resolve);
const invest = resolve => require(['./components/invest.vue'], resolve);
const fund = resolve => require(['./components/fund.vue'], resolve);
const account = resolve => require(['./components/account.vue'], resolve);
export const routes = [
	{
		name:'index',
		path: '/',
		component: index
	},
  {
    name:'invest',
    path: '/invest',
    component: invest
  },
   {
    name:'fund',
    path:'/fund',
    component:fund
  },
  {
    name:'account',
    path: '/account',
    component: account
  }
  ]



