import Vue from 'vue'
import VueRouter from 'vue-router'
import './assets/css/index.css'
import './assets/css/style.css'
import {routes} from './routes.js'
Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes,
})

const app = new Vue({
  router: router
}).$mount('#app')



